import React, { useEffect, useState } from 'react'
import { Button } from '@material-ui/core'
import { Controller, SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useLocation } from 'react-router-dom'
import { yupResolver } from '@hookform/resolvers/yup'
import OnboardingSchema from '../../../../utils/validation-schemas/onboarding-schemas'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import {
  Cohort,
  Course,
  Education,
  Experience,
  ExperienceInfo,
  PPExpType,
  PPSpendTime,
  ProjectsInfo,
} from '../../../../services/types/onboarding/onboarding-types'
import { getOnboardingBaseData } from '../../../../services/axios/onboarding'
import { ControlledCourseField, ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import useStyles from './OnboardingFormProjects.styles'
import ButtonCounter from '../ButtonCounter/ButtonCounter'

const OnboardingFormProjects = () => {
  const classes = useStyles()
  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useAppDispatch()

  const avatar = location.state?.avatar
  const { baseData } = useAppSelector((store) => store.onboarding)
  const personalInfo = JSON.parse(localStorage.getItem('personalInfo') as string)
  const projectsInfo = JSON.parse(localStorage.getItem('projectsInfo') as string)

  const defaultValues = projectsInfo || {
    pet_project_experience_type: null,
    pet_project_spend_time: null,
    pet_project_count: 0,
    pet_project_dreams: '',
  }

  const {
    control,
    handleSubmit,
    register,
    setValue,
    watch,
    getValues,
    formState: { errors },
  } = useForm<ProjectsInfo>({
    defaultValues,
    // resolver: yupResolver(onboardingExperienceSchema),
    mode: 'onChange',
  })

  const [types, setTypes] = useState<PPExpType[]>([])
  const [times, setTimes] = useState<PPSpendTime[]>([])

  useEffect(() => {
    if (!personalInfo) {
      navigate('/onboarding/personal-info')
    }
  }, [personalInfo])

  useEffect(() => {
    if (!baseData) dispatch(getOnboardingBaseData())

    if (baseData?.pet_project_experience_type) setTypes(baseData.pet_project_experience_type)
    if (baseData?.pet_project_spend_time) setTimes(baseData.pet_project_spend_time)
  }, [baseData])

  const onSubmit: SubmitHandler<ProjectsInfo> = (formData) => {
    localStorage.setItem('projectsInfo', JSON.stringify(formData))
    navigate('/onboarding/resume', { state: { avatar } })
  }

  const getTimeDirItems = (val: PPSpendTime) => {
    let name
    if (!val?.max) name = `> ${val.min} часов`
    else name = `${val.min} - ${val.max} часов`
    return { id: val.id, name }
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <fieldset className={classes.fieldset}>
          <Controller
            name="pet_project_experience_type"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Участвовал ли ты в каком-либо проекте по специальности, которой сейчас обучаешься?"
                tipContent="Выбери из списка"
                value={value || []}
                onChange={onChange}
                error={!!errors?.pet_project_experience_type?.message}
                helperText={errors?.pet_project_experience_type?.message}
                items={types?.map((t) => ({ id: t?.id, name: t?.value }))}
                maxWidth={386}
              />
            )}
          />
          <Controller
            name="pet_project_spend_time"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ControlledSelectField
                labelTitle="Сколько часов в неделю ты готов уделять участию в Pet-проекте или проекте Мастерской? "
                tipContent="Выбери из списка"
                value={value || []}
                onChange={onChange}
                error={!!errors?.pet_project_spend_time?.message}
                helperText={errors?.pet_project_spend_time?.message}
                items={times?.map(getTimeDirItems)}
                maxWidth={386}
              />
            )}
          />
          <Controller
            name="pet_project_count"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ButtonCounter
                labelTitle="Кол-во Pet-проектов, в которых ты сейчас принимаешь участие"
                value={value || 0}
                onChange={onChange}
              />
            )}
          />
          <ControlledTextField
            labelTitle="Твои ожидания от проектов"
            fieldName="pet_project_dreams"
            placeholder="Расскажи, зачем ты принимаешь в них участие"
            control={control}
            error={!!errors.pet_project_dreams?.message}
            helperText={errors.pet_project_dreams?.message}
            maxWidth={386}
            multiline
            rows={5}
          />

          <div className={classes.buttonContainer}>
            <Button
              type="button"
              className={`${classes.formButton} ${classes.previousButton}`}
              onClick={() => {
                const values = getValues()
                localStorage.setItem('projectsInfo', JSON.stringify(values))
                navigate('/onboarding/experience', { state: { avatar } })
              }}
            >
              Назад
            </Button>

            <Button className={classes.formButton} type="submit">
              Далее
            </Button>
          </div>
        </fieldset>
      </form>
    </>
  )
}

export default OnboardingFormProjects
